<template>
  <div :class="[activeClass,'tool ']">
    <div class="nav " >
      <div class="backhome ">首页</div>
      <div class="title">{{navTitle}}</div>
    </div>
    <div class="footer ">
      <span @click="clickHandle(item)" v-for="(item,index) in navList" :key="index">{{item.title}}</span>
      <!-- <span>音乐</span>
      <span>书籍</span>
      <span>聊天</span>-->
    </div>
  </div>
</template>
<script>
export default {
  name: "Tool",
  //   props: {
  //     msg: String
  //   },
  data() {
    return {
      navTitle: "电影",
      activeClass: "movie",
      navList: [
        {
          title: "电影",
          path: "/",
          navClass: "movie"
        },
        {
          title: "音乐",
          path: "/music",
          navClass: "music",
        },
        {            
          title: "书籍",
          path: "/book",
          navClass: "book",
        },
        {
          title: "聊天",
          path: "/talk",
          navClass: "talk",
        }
      ]
    };
  },
  created() {
    //   this.$router.push(window.location);
    // console.log(window.location.pathname);
    let arr = this.navList
    arr.forEach((val,index) => {
        if (arr[index].path==window.location.pathname) {
            this.clickHandle(arr[index])
            // console.log(arr[index]);
            
        }
    });
    
    
  },
  methods: {
    clickHandle(obj) {
      this.$router.push(obj.path);
    //   console.log(obj);
      this.activeClass = obj.navClass;
      this.navTitle = obj.title
    }
  }
};
</script>
<style scoped>
* {
  margin: 0;
  padding: 0;
}
.tool {
  font-size: 14px;
  position: relative;
  /* color: antiquewhite; */
}
.footer {
  width: 100%;
  height: 1rem;
  position: fixed;

  right: 0;
  bottom: 0;
}
.nav {
  width: 100%;
  height: 1rem;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
}
.backhome,
.title {
  float: left;
  height: 1rem;
  line-height: 1rem;
}
.backhome {
  margin-left: 10px;
}
.title {
  position: fixed;
  right: 50%;
  transform: translateX(50%);
  font-size: 35px;
}
.footer {
  line-height: 1rem;
  display: flex;
  text-align: center;
}
.footer span {
  /* margin: 0 25px; */
  flex-grow: 1;
}
.movie .nav,
.movie .footer {
  background-color: rgb(33, 150, 243);
}
.music .nav,
.music .footer {
  background-color: rgb(0, 150, 136);
}
.book .nav,
.book .footer {
  background-color: rgb(121, 85, 72);
}
.talk .nav,
.talk .footer {
  background-color: rgb(63, 81, 181);
}
</style>

